</template>

<script>
import { clientRanking } from "@api/public";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
import { Icon } from "vant";
export default {
  name: "ClientRanking",
  components: { 
    Empty,
    Loadings,
    [Icon.name]: Icon,
  },
  props: {},
  data: function () {
    return {
      data: null,
    };
  },
  created() {

  },
  mounted: function () {},
  methods: {
    toThousands(num) {
      var result = [],
        counter = 0;
      num = (num || 0).toString().split("");
      for (var i = num.length - 1; i >= 0; i--) {
        counter++;
        result.unshift(num[i]);
        if (!(counter % 3) && i != 0) {
          result.unshift(",");
        }
      }
      return result.join("");
    },
  },
};
</script>
